img{
    max-width: 100%;
}
.news-front{
    display: grid;
    grid-template-columns: 40% repeat(2, 1fr);
    grid-template-rows: calc(250px + 5vw) calc(120px + 5vw);
    grid-template-areas:
    "l . ."
    "l d d"
    ;
    gap: 2%;
    article{
        display: grid;
        grid-template-rows: 60% 40%;
    }
    .large{
        grid-area: l;
        display: grid;
        grid-template-rows: 80% 20%;
    }
    .double{
        grid-area: d;
        grid-template-columns: 50% 50%;
        grid-template-rows: none;
        flex-direction: row;
        align-items: center;
    }
}
.news-latest{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, calc(250px + 5vw));
    gap: 2%;
}
.img-link{
    height: 100%;
    width: 100%;
}
.news-text{
    border: 1px solid #d3d3d3;
    padding: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
}

article{
    display: flex;
    flex-direction: column;
}
.sticky-inside{
    position: sticky;
    top: 0;
}
.news-sec{
    display: grid;
    grid-template-columns: 78% 20%;
    gap: 2%;
}
.news-second{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, calc(250px + 5vw));
    gap: 2%;
    article{
        position: relative;
    }
    .news-second-text{
        padding: calc(5px + .5vw);
        background: #ffffff;
        position: absolute;
        bottom: 10%;
        width: 90%;
    }
}
.news-latest-full{
    .news-three-category{
    padding-bottom: 4%;
    span{
        width: 24%;
        font-size: 20px;
    }
    }
}
.news-three-category{
    text-transform: uppercase;
    display: flex;
    align-items: center;
    padding-bottom: 6%;
    font-weight: bold;
    span{
        padding-right: 6%;
    }
    &::after{
        content: "";
        margin-left: auto;
        height: calc(4px + 0.2vw);
        width: 100%;
        background: #f5f5f5;
    }
}
.news-three{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: calc(450px + 5vw);
    gap: 2%;
    article{
        position: relative;
    }
    
    .news-three-text{
        padding: calc(5px + .5vw);
        position: absolute;
        bottom: 0;
        text-align: center;
        a{
            color: #ffffff;
        }
        .label{
            background: #ea1717;
            padding: 0 10%;
        }
    }
}
.trending{
    display: flex;
    padding: 20px;
    margin: 50px auto;
    width: 70%;
    background: #eaeaea;
}
.trending-txt{
text-transform: uppercase;
color: #ffffff;
background: #ea1717;
padding: 0.7% 20px;
margin-right: 2%;
}
.swiper-slide{
    display: flex;
    text-transform: uppercase;
    align-items: center;
    font-weight: bold;
}
.prev, .next{
    width: 25px;
    height: 25px;
    border: 1px solid #d3d3d3;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover{
        background: #ea1717;
    }
}
.next{
    right: 0;
    &::before{
        content: ">";
        font-size: 12px;
    }
    &:hover::before{
        color: #ffffff;
    }
}
.prev{
    right: 30px;
    &::before{
        content: "<";
        font-size: 12px;
    }
    &:hover::before{
        color: #ffffff;
    }
}
.prev-and-next{
    width: 100%;
}
.banner-between{
    padding: 8%;
    text-align: center;
}
.demos-buy{
    position: fixed;
    right: -1px;
    top: 10%;
    a{
        display: block;
        text-transform: uppercase;
        font-size: 12px;
        text-decoration: none;
        border: 1px solid #e8e8e8;
        color: #000000;
        height: 30px;
        width: 80px;
        margin: 4% 0;
        text-align: center;
        padding: 8px 0;
        background: #ffffff;
        &:last-child{
            background: #f4f4f4;
        }
    }
}